h1{
  text-align: center;
  
font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
font-style: italic;

}
.starter:hover{
  scale: 1.3;
 text-decoration: underline;
 transition: 2s;
}

.starter{
  font-size: 100%;
  animation: right 2s ease forwards;
  color: cyan;
}

.txt{
  position: relative;


 font-size: 300%;
 margin-top: 20%;
 margin-right: 30%;
}

@keyframes right {
  0%{
    transform:translateX(-200px);
    opacity:0;
    
  }
  100%{
    transform:translateX(0px);
    opacity:1;
 
  }
}
@keyframes left{
  0%{
    transform:translateX(200px);
    opacity:0;
  }
  100%{
    transform:translateX(0px);
    opacity:1;
  }
}
@keyframes bottom{
  0%{
    transform:translateY(200px);
    opacity:0;
  }
  100%{
    transform:translateY(0px);
    opacity:1;
  }
}
.bi-person{
  color: white;
  border: 1px solid white;
  border-radius: 50%;
  padding: 5px;
  position: sticky;
  left: 95%;
}

.hide{
  height: 200px;
  width: 200px;
  
  z-index: 1;
  top: 5%;
  left: 85%;
   display:none;
}
.bt{
  background-color: transparent;
  border: 0px;
  animation: right 2s ease forwards;
}
.bt:hover + .hide {
  display: block;
  height: 200px;
  width: 200px;
  
  z-index: 1;
  top: 5%;
  left: 85%;
}
