/* Let's highlight canvas boundaries */

/* Reset some default styling */
.gjs-cv-canvas {
  top: 50;
  width: 85%;
  height: auto;
}

.gjs-block {
  background-color: black;
  color: white;
  border: 1px solid white;
}

.gjs-block:hover {
  color: cyan;
  box-shadow: 2px 2px 5px cyan;
  border: 1px solid cyan;
}
.panel__top {
  padding: 0;
  width: 100%;
  display: flex;
  position: initial;
  justify-content: center;
  justify-content: space-between;
}
.panel__basic-actions {
  position: initial;
}
.editor-row {
  display: flex;
  justify-content: flex-start;
  align-items: stretch;
  flex-wrap: nowrap;
  height: 300px;
}

.editor-canvas {
  flex-grow: 1;
}

.panel__right {
  flex-basis: 230px;
  position: relative;
  overflow-y: auto;
}

:root {
  --gjs-primary-color: #010101;
  --gjs-secondary-color: #fff;
  --gjs-tertiary-color: cyan;
  --gjs-quaternary-color: cyan;
}